import React, { useState } from 'react';
import { width } from 'window-size';

const axios = require('axios');

function Achievement(props) {
    const [user] = useState(props.user);
    let Badges = [];

    let data = {
        valid: (user.inscript / 1000 < 1551398400) ? true : false,
        icon: "01.png",
        title: "First",
        name: "beta",
        hide: true,
        messag: "Attribu au joueur inscrit avant le 01/3/2019 (pendant la Bta)"

    }
    Badges.push(<Badge data={data} user={user} key={data.name} />)

    if (user.achievement && user.achievement.konami) {
        data = {
            valid: (user.inscript / 1000 < 1551398400) ? true : false,
            icon: "konami.png",
            title: "OldSchool",
            name: "konami",
            hide: true,
            messag: "U | U | D | D | L | R | L | R | B | A"

        }
        Badges.push(<Badge data={data} user={user} key={data.name} />)
    }

    data = {
        valid: (user.btag) ? true : false,
        icon: "03.png",
        title: "Loot'O'Vor",
        hide: true,
        name: "btag",
        messag: "Miam"
    }
    Badges.push(<Badge data={data} user={user} key={data.name} />)

    data = {
        valid: (user.btag && user.mail) ? true : false,
        icon: "02.png",
        title: "ValidUser",
        name: "validUser",
        hide: true,
        messag: "Attribu au joueur ayant rempli leurs profil"
    }
    Badges.push(<Badge data={data} user={user} key={data.name} />)

    data = {
        valid: (user.loveTeam) ? true : false,
        icon: "05.png",
        title: "Mon Equipe de Coeur",
        name: "loveTeam",
        hide: true,
        messag: "Attribu au joueur ayant choisie une Equipe de Coeur"
    }
    Badges.push(<Badge data={data} user={user} key={data.name} />)

    data = {
        valid: (user.bets) ? true : false,
        state: 0,
        icon: "bet.png",
        title: "Pronostiqueur en herbe",
        name: "bets-0",
        hide: false,
        messag: "Pronostiqu 1 match"
    }
    if (user.bets && Object.keys(user.bets).length >= 1) {
        data.state += 1;
        data.messag = Object.keys(user.bets).length + "/5"
        data.title = "Sorti de l'Iceberg";
        data.name = "bets-" + data.state;
    }
    if (user.bets && Object.keys(user.bets).length >= 5) {
        data.state += 1;
        data.messag = Object.keys(user.bets).length + "/10"
        data.title = "Paresseux";
        data.name = "bets-" + data.state;
    }
    if (user.bets && Object.keys(user.bets).length >= 10) {
        data.state += 1;
        data.messag = Object.keys(user.bets).length + "/15"
        data.title = "Initi";
        data.name = "bets-" + data.state;
    }
    if (user.bets && Object.keys(user.bets).length >= 15) {
        data.state += 1;
        data.messag = Object.keys(user.bets).length + "/25"
        data.title = "Pile AA";
        data.name = "bets-" + data.state;
    }
    if (user.bets && Object.keys(user.bets).length >= 25) {
        data.state += 1;
        data.messag = Object.keys(user.bets).length + "/50"
        data.title = "Un million de frappes";
        data.name = "bets-" + data.state;
    }
    if (user.bets && Object.keys(user.bets).length >= 50) {
        data.state += 1;
        data.messag = Object.keys(user.bets).length + "/100"
        data.title = "Dlit d'acharnement";
        data.name = "bets-" + data.state;
    }
    if (user.bets && Object.keys(user.bets).length >= 100) {
        data.state += 1;
        data.messag = Object.keys(user.bets).length + "/150"
        data.title = "A rythme de 7H par jour";
        data.name = "bets-" + data.state;
    }
    if (user.bets && Object.keys(user.bets).length >= 150) {
        data.state += 1;
        data.messag = Object.keys(user.bets).length + "/250"
        data.title = "A rythme de 12H par jour";
        data.name = "bets-" + data.state;
    }
    // TOO HIGH
    if (user.bets && Object.keys(user.bets).length >= 250) {
        data.state = 8;
        data.messag = Object.keys(user.bets).length + "/500"
        data.title = "A rythme de 20H par jour";
        data.name = "bets-" + data.state;
    }
    if (user.bets && Object.keys(user.bets).length >= 500) {
        data.state = 8;
        data.messag = Object.keys(user.bets).length + "/1000"
        data.title = "?? ?? ??";
        data.name = "bets-" + data.state;
    }
    if (user.bets && Object.keys(user.bets).length >= 1000) {
        data.state = 8;
        data.messag = Object.keys(user.bets).length + "/"
        data.title = "Error 404";
        data.name = "bets-" + data.state;
    }

    Badges.push(<Badge data={data} user={user} key={data.name} />)


    let keyBets = Object.keys(user.bets)
    let countLinkBetsValid = 0;
    let countLinkBetsFalse = 0;
    keyBets.map((key) => {
        if (user.bets[key].valid) {
            const winTeam = (user.bets[key].resultA > user.bets[key].resultB) ? "A" : "B";
            const winBet = (user.bets[key].scoreA > user.bets[key].scoreB) ? "A" : "B";

            if (winTeam == winBet) {
                countLinkBetsValid += 1;
            }
            else {
                countLinkBetsFalse += 1;
            }

            // console.log(linkBetsValid);
            // console.log(countLinkBetsValid);
            // console.log(linkBetsFalse);
            // console.log(countLinkBetsFalse);
        }
    })

    if (countLinkBetsValid >= 1) {
        data = {
            valid: true,
            state: 1,
            icon: "bet-true.png",
            title: "Le dbut de la gloire",
            name: "bets-true-1",
            hide: false,
            messag: `${countLinkBetsValid}/5`
        }
    }
    if (countLinkBetsValid >= 5) {
        data = {
            valid: true,
            state: 2,
            icon: "bet-true.png",
            title: "Un Pas de plus",
            name: "bets-true-2",
            hide: false,
            messag: `${countLinkBetsValid}/10`
        }
    }
    if (countLinkBetsValid >= 10) {
        data = {
            valid: true,
            state: 3,
            icon: "bet-true.png",
            title: "Le chemin est long",
            name: "bets-true-3",
            hide: false,
            messag: `${countLinkBetsValid}/25`
        }
    }
    Badges.push(<Badge data={data} user={user} key={data.name} />)


    if (countLinkBetsFalse >= 1) {
        data = {
            valid: true,
            state: 1,
            icon: "bet-false.png",
            title: "Boulet de Bronze",
            name: "bets-false-1",
            hide: false,
            messag: `${countLinkBetsFalse}/5`
        }
    }
    if (countLinkBetsFalse >= 5) {
        data = {
            valid: true,
            state: 2,
            icon: "bet-false.png",
            title: "Boulet d'Argent",
            name: "bets-false-2",
            hide: false,
            messag: `${countLinkBetsFalse}/10`
        }
    }
    if (countLinkBetsFalse >= 10) {
        data = {
            valid: true,
            state: 3,
            icon: "bet-false.png",
            title: "Boulet d'Or",
            name: "bets-false-3",
            hide: false,
            messag: `${countLinkBetsFalse}/25`
        }
    }
    if (countLinkBetsFalse >= 25) {
        data = {
            valid: true,
            state: 4,
            icon: "bet-false.png",
            title: "Boulet de Platine",
            name: "bets-false-4",
            hide: false,
            messag: `${countLinkBetsFalse}/50`
        }
    }

    Badges.push(<Badge data={data} user={user} key={data.name} />)

    function shuffle(a) {
        // for (let i = a.length - 1; i > 0; i--) {
        //     const j = Math.floor(Math.random() * (i + 1));
        //     [a[i], a[j]] = [a[j], a[i]];
        // }
        return a;
    }


    return (
        <div className="achievement flex">
            <h3>Haut Fait</h3>
            {shuffle(Badges)}
        </div>
    )
}




function Badge({ data, user }) {
    const [hover, setHover] = useState(false);
    if (data.valid == false && data.hide == true) {
        return '';
    } else {
        const parseMessage = data.messag.split('/');
        
        
        if (user.login) {
            if(user.achievement){
                if(!user.achievement[data.name]){
                    axios.post('../api/sendAchievement', {
                        achievement: data.name,
                        user: user.login,
                    }).catch(function (error) {
                        console.log(error);
                    });
                }
            
        }else{
            axios.post('../api/sendAchievement', {
                achievement: data.name,
                user: user.login,
            }).catch(function (error) {
                console.log(error);
            });
        }
    }


        return (<div
            className={(data.valid) ? 'Badge true' : 'Badge false'}
            onMouseEnter={() => {
                setHover(true);
            }} onMouseLeave={() => {
                setHover(false);
            }}>
            <div className={(hover == true) ? 'messag show' : 'messag'}>
                <div className="title">
                    {data.title}
                </div>
                {(data.state) ?
                    <div>
                        <img src={`../imgs/hf/T${data.state}.png`} alt="" />
                        {(parseMessage.length > 1) ?
                            <progress id="file" max={parseMessage[1]} value={parseMessage[0]}> {parseMessage[0]}</progress> : ''}
                    </div>
                    : data.messag}
            </div>
            <img src={`../imgs/hf/${data.icon}`} alt="" />
            {(data.state) ?
                <div className="state">
                    <img src={`../imgs/hf/T${data.state}.png`} alt="" />
                </div>
                : ''}
        </div>)
    }

}

export default Achievement;