import React, { useState, useEffect } from 'react';
import { Redirect, withRouter } from 'react-router-dom';
import cookie from 'react-cookies';

import Switch from "react-switch";
import Modal from '../../components/Modal';
import Achievement from '../../components/Achievement';
import UserBets from '../../components/UserBets';

const axios = require('axios');

function Profil({ match, history }) {



    const [modal, setModal] = useState({
        show: false,
        message: ""
    });
    function toggleModal(clean = false) {
        setModal({
            show: !modal.show,
            message: (clean) ? modal.message : ""
        })
    }

    function sendProfil() {
        console.log('sendProfil');
        setModal({
            show: true,
            message: ''
        });
        axios.post('../api/updatePlayer', {
            user: user,
        })
            .then(function (response) {
                setModal({
                    show: true,
                    message: response.data.message
                });
                if (response.data.redirect) {
                    setTimeout(() => {
                        cookie.remove('login');
                        cookie.remove('user');
                        cookie.remove('bets');
                        history.push('/');
                    }, 5000)
                }
                setTimeout(() => {
                    setModal({
                        show: false,
                        message: ''
                    });
                }, 4500)
            })
            .catch(function (error) {
                console.log(error);
            });

    }
    console.log("Profil", match.params.user);
    const [userName, setUserName] = useState(match.params.user);
    const [user, setUser] = useState(null);



    if (user != null && userName == match.params.user) {
        if (user.error) {
            return (
                <div className="Profil">
                    <h2>{user.login}</h2>
                </div>

            )
        }
        return (
            <div className="Profil">
                <h2>{user.name}</h2>
                <Achievement user={user} />
                {(user.bets)?<UserBets user={user} />: ''}
                
            </div>
        )

    }
    else if (userName != match.params.user) {
        setUserName(match.params.user);

    }
    else {
        if (userName != null) {
            axios.get(`../api/getPlayer/${userName}`)
                .then(function (response) {
                    setUser(response.data);
                })
                .catch(function (error) {
                    console.log(error);
                });
        }

        return (
            <div className="Profil">
                <h2>{match.params.user}</h2>
            </div>
        );
    }


}



export default withRouter(Profil);