import React, { useState } from 'react';

function UserBets({ user }) {


    const keyBets = Object.keys(user.bets)

    let listBets = keyBets.map((key) => {
        const currentBet = user.bets[key];
        if (currentBet.valid) {
            let className = 'bet flex three';
            if (currentBet.score == 0) {
                className += ' null'
            }
            return (
                <div className={className} key={key}>

                    <div className="TeamA">{currentBet.TeamA}</div>
                    <div>R : </div>
                    <div>{currentBet.resultA}</div>
                    <div>{currentBet.resultB}</div>
                    <div>P :</div>
                    <div>{currentBet.scoreA}</div>
                    <div>{currentBet.scoreB}</div>
                    <div className="TeamB">{currentBet.TeamB}</div>
                    <div className="score">{currentBet.score}</div>
                </div>
            )
        }


    })

    return (
        <div className="UserBets">
            <h3>Pronostic</h3>
            <div className="flex center six">
                {listBets}

            </div>
        </div>
    )

}

export default UserBets;